'use client';

import { useEffect, useState } from 'react';
import {
  FormConfiguration,
  FormField,
  FormFieldType,
  FormSection,
} from '@/src/lib/formConfigurations/formConfigurationTypes';
import {
  getFormConfigurationByCompanyId,
  saveFormConfiguration,
} from '@/src/lib/services/Configirations/formConfigurationService';
import { Button } from '@/src/components/ui/button';
import { Input } from '@/src/components/ui/input';
import { SimpleSelect } from '@/src/components/ui/select';
import { v4 as uuidv4 } from 'uuid';

interface Props {
  companyId: string;
  userId: string;
}

const fieldTypes: FormFieldType[] = [
  'text',
  'number',
  'date',
  'textarea',
  'checkbox',
  'select',
];

export default function FormBuilder({ companyId, userId }: Props) {
  const [config, setConfig] = useState<FormConfiguration | null>(null);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    const fetchConfig = async () => {
      const result = await getFormConfigurationByCompanyId(companyId);
      setConfig(result);
    };
    fetchConfig();
  }, [companyId]);

  const addSection = () => {
    const newSection: FormSection = {
      id: uuidv4(),
      title: 'New Section',
      fields: [],
    };
    setConfig((prev) =>
      prev ? { ...prev, sections: [...prev.sections, newSection] } : prev
    );
  };

  const addField = (sectionId: string) => {
    const newField: FormField = {
      key: `field_${Date.now()}`,
      label: 'New Field',
      type: 'text',
      required: false,
      visible: true,
    };
    setConfig((prev) =>
      prev
        ? {
            ...prev,
            sections: prev.sections.map((section) =>
              section.id === sectionId
                ? { ...section, fields: [...section.fields, newField] }
                : section
            ),
          }
        : prev
    );
  };

  const updateField = (
    sectionId: string,
    fieldIndex: number,
    updatedField: Partial<FormField>
  ) => {
    setConfig((prev) =>
      prev
        ? {
            ...prev,
            sections: prev.sections.map((section) =>
              section.id === sectionId
                ? {
                    ...section,
                    fields: section.fields.map((field, idx) =>
                      idx === fieldIndex ? { ...field, ...updatedField } : field
                    ),
                  }
                : section
            ),
          }
        : prev
    );
  };

  const removeField = (sectionId: string, fieldIndex: number) => {
    setConfig((prev) =>
      prev
        ? {
            ...prev,
            sections: prev.sections.map((section) =>
              section.id === sectionId
                ? {
                    ...section,
                    fields: section.fields.filter((_, idx) => idx !== fieldIndex),
                  }
                : section
            ),
          }
        : prev
    );
  };

  const save = async () => {
    if (!config) return;
    setSaving(true);
    await saveFormConfiguration({
      ...config,
      createdBy: userId,
      companyId,
    });
    setSaving(false);
  };

  if (!config) return <div>Loading form configuration...</div>;

  return (
    <div className="space-y-6 p-4">
      <h2 className="text-xl font-bold">Form Builder</h2>

      {config.sections.map((section) => (
        <div key={section.id} className="border p-4 rounded space-y-4">
          <Input
            value={section.title}
            onChange={(e) =>
              setConfig((prev) =>
                prev
                  ? {
                      ...prev,
                      sections: prev.sections.map((s) =>
                        s.id === section.id ? { ...s, title: e.target.value } : s
                      ),
                    }
                  : prev
              )
            }
            className="font-semibold text-lg"
          />

          {section.fields.map((field, index) => (
            <div
              key={field.key}
              className="grid grid-cols-1 md:grid-cols-4 gap-2 items-end"
            >
              {/* Label input */}
              <Input
                value={field.label}
                onChange={(e) =>
                  updateField(section.id, index, { label: e.target.value })
                }
                placeholder="Field Label"
              />

              {/* Field type select */}
              <SimpleSelect
                options={fieldTypes.map((t) => ({ label: t, value: t }))}
                value={field.type}
                onChange={(type) =>
                  updateField(section.id, index, {
                    type: type as FormFieldType,
                  })
                }
                placeholder="Select field type"
              />

              {/* Required checkbox */}
              <label className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={field.required}
                  onChange={(e) =>
                    updateField(section.id, index, {
                      required: e.target.checked,
                    })
                  }
                />
                <span>Required</span>
              </label>

              {/* Delete button */}
              <Button
                variant="danger"
                onClick={() => removeField(section.id, index)}
              >
                Delete
              </Button>
            </div>
          ))}

          <Button onClick={() => addField(section.id)}>‚ûï Add Field</Button>
        </div>
      ))}

      <div className="flex gap-4">
        <Button onClick={addSection}>‚ûï Add Section</Button>
        <Button onClick={save} disabled={saving}>
          üíæ {saving ? 'Saving...' : 'Save Form'}
        </Button>
      </div>
    </div>
  );
}
