export type FormFieldType =
  | 'text'
  | 'number'
  | 'select'
  | 'checkbox'
  | 'date'
  | 'textarea';

export interface FormField {
  key: string;
  label: string;
  type: FormFieldType;
  required: boolean;
  visible: boolean;
  options?: string[]; // for "select"
  conditional?: {
    dependsOn: string;
    valueEquals: string | number | boolean;
  };
}

export interface FormSection {
  id: string;
  title: string;
  fields: FormField[];
}

export interface FormConfiguration {
  id?: string; // Firestore document ID
  companyId: string;
  createdBy: string;
  sections: FormSection[];
  createdAt?: any; // Timestamp | Date
  updatedAt?: any;
}

